<template>
	<view>
		<view class="uni-padding-wrap uni-common-mt">
						<uni-segmented-control :current="current" :values="items" :style-type="styleType"
							:active-color="activeColor" @clickItem="onClickItem" />
		</view>
		<view  class="news_item"   v-for="item in newsList" :key="item.id">
			<view v-if="current===0" >
				<uni-card :title="item.content"  :extra="item.address"  padding="10px 0" :thumbnail="avatar" isShadow="true"  shadow="0 6px 20px 0 rgba(0, 0, 0, 0.19)">
					<view hover-stop-propagation="true">
						<image style="width: 100%;"   @click="goDetail(index)" src="https://img-prod-cms-rt-microsoft-com.akamaized.net/cms/api/am/imageFileData/RE4wtd6?ver=364f"></image>
					</view>
								
								<text class="uni-body uni-mt-5">维修详情</text>
								
								<uni-row>
									<uni-col :span="16" pull="1">
										<uni-list>
											<uni-list-item title="维修人:" :rightText="repaire" clickable="true" @click="pickPerson('showLeft')" ></uni-list-item>
										</uni-list>
									</uni-col>
									<uni-col :span="8" push="0">
										<button class="button popup-success" type="primary" @click="submit(index)">分配订单</button>
									</uni-col>
								</uni-row>
							</uni-card>
			</view>
			<view v-if="current===1">
				<uni-card :title="item.content"  @click="goDetail(index)" isShadow="true" shadow="0 6px 20px 0 rgba(0, 0, 0, 0.19)"  thumbnail="https://img-prod-cms-rt-microsoft-com.akamaized.net/cms/api/am/imageFileData/RE4wtd6?ver=364f"  >
					<text >{{item.address}}</text>
				</uni-card>
			</view>
		</view>
		<view v-if="current===1">
			<uni-fab ref="fab" :pattern="pattern" :content="content" :horizontal="horizontal" :vertical="vertical"
						:direction="direction" @trigger="trigger" @fabClick="fabClick(e)" />
		</view>
		<view>
			<uni-drawer ref="showLeft" mode="left" :width="250" @change="change($event,'showLeft')">
				<view class="close">
					<view v-for="(item,index) in Person" @click="choose(index)">
						<uni-list>
							<uni-list-item :title="item" ></uni-list-item>
						</uni-list>
					</view>
					
					<button @click="closeDrawer('showLeft')" type="primary"><text >取消选择</text></button>
				</view>
			</uni-drawer>
		</view>
		<view>
					<!-- 提示窗示例 -->
				<uni-popup ref="alertDialog" type="dialog">
					<uni-popup-dialog :type="msgType" confirmText="同意" cancelText="关闭"  title="警告" content="确认分配此订单？" @confirm="dialogConfirm"
						@close="dialogClose()">
					</uni-popup-dialog>
				</uni-popup>
		</view>
	</view>
</template>

<script>
	
		export default {
			components: {},
			data() {
				return {
					repaire:"",
					newsList:[{
						content:"水管坏了",
						address:"荔园食堂背后",
						id:2
					}],
					
					Person:["张三","李四","王五"],
					index:0, //代表选中【全部订单，已受理，派工，完工】 中的索引值，根据索引值进行渲染
					items: ['未分配订单', '已分配订单'],
					options2: [{
											text: '取消',
											style: {
												backgroundColor: '#007aff'
											}
										},
										{
											text: '确认',
											style: {
												backgroundColor: '#F56C6C'
											}
										}
							],
					styles: [{
							value: 'button',
							text: '按钮',
							checked: true
						},
						{
							value: 'text',
							text: '文字'
						}
					],
					current: 0,
					activeColor: '#007aff',
					styleType: 'button',
					newsList:[{
						content:"维修问题",
						address:"维修地点"
					},
					{
						content:"维修问题",
						address:"维修地点"
					}
					],
					
					directionStr: '垂直',
					horizontal: 'left',
					vertical: 'bottom',
					direction: 'horizontal',
					pattern: {
						color: '#7A7E83',
						backgroundColor: '#fff',
						selectedColor: '#007AFF',
						buttonColor: '#007AFF',
						iconColor: '#fff'
					},
					is_color_type: false,
					content: [{
							iconPath: '/static/file.png',
							selectedIconPath: '/static/3.png',
							text: '全部订单',
							active: true
						},
					{
						iconPath: '/static/11111.png',
						selectedIconPath:'/static/shouli.png',
						text:'已受理',
						active:false
					},
					{
						iconPath: '/static/shigongrenyuan.png',
						selectedIconPath: '/static/4.png',
						text: '派工',
						active: false
					},
					{
						iconPath: '/static/complete.png',
						selectedIconPath: '/static/2.png',
						text: '完工',
						active: false
					}
					]
				}
			},
			methods: {
				onClickItem(e) {
					if (this.current !== e.currentIndex) {
						this.current = e.currentIndex
					}
				},
				goDetail(index){
					console.log("点击了组件")
					uni.navigateTo({
						url:"historyDetail",
						
					})
				},
				trigger(e) {
								console.log(e)
								this.content[0].active=false;
								this.content[1].active=false;
								this.content[2].active=false;
								this.content[3].active=false;
								this.content[e.index].active = !e.item.active
								this.index=e.index
								console.log(this.index)
							},
				pickPerson(e){
					console.log("dianjiale");
					this.$refs[e].open();
				},
				
				closeDrawer(e){
					this.$refs[e].close();
				},
				
				choose(index){
					console.log("选择了人员，序号为：",index);
					this.repaire=this.Person[index];
					this.$refs['showLeft'].close();
				},
				
				submit(index){
					console.log("订单index: ",index);
					this.$refs.alertDialog.open()
				},
				
				change(e,s){
					console.log(e);
				},
				
				dialogClose(){
					console.log("关闭弹窗")
				}
			}
		}
</script>

<style lang="scss">
	@import "@/static/iconfont.css";
	.uni-common-mt {
			margin-top: 30px;
		}
		
	.uni-padding-wrap {
		// width: 750rpx;
		padding: 0px 30px;
	}
</style>